.bottom-panel {
  position: absolute;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  background: -webkit-radial-gradient(80% 120% ellipse at 50% 65%, rgba(231, 231, 231, 0.8) 25%, rgba(170, 170, 170, 0.8) 100%);
  background: -o-radial-gradient(80% 120% ellipse at 50% 65%, rgba(231, 231, 231, 0.8) 25%, rgba(170, 170, 170, 0.8) 100%);
  background: radial-gradient(80% 120% ellipse at 50% 65%, rgba(231, 231, 231, 0.8) 25%, rgba(170, 170, 170, 0.8) 100%);
  border-top: 1px solid #aaa;
  font-size: 1em;
  height: 3em;
  /*  box-shadow:
    -2px 0px 6px 1px rgba(0, 0, 0, 0.14),
    -2px 0px 8px 3px rgba(0, 0, 0, 0.12);*/ }
  .bottom-panel button {
    border: none;
    background: none;
    cursor: pointer;
    position: absolute;
    opacity: 0.85; }
    .bottom-panel button:hover {
      opacity: 1; }
    .bottom-panel button.contact {
      right: 4.5em;
      width: 5em; }
      .bottom-panel button.contact .icon {
        fill: #002f60;
        height: 2.5em;
        width: 3.25em; }
  .bottom-panel .logo {
    position: absolute;
    left: 0.25em;
    bottom: 0;
    height: 5.35em;
    width: 14em;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
  .bottom-panel ul.lang {
    position: absolute;
    right: 0.75em;
    padding: 0; }
    .bottom-panel ul.lang li {
      display: inline-block;
      padding: 0 0.35em;
      cursor: pointer;
      color: #555; }
      .bottom-panel ul.lang li.active {
        color: #dc002e;
        cursor: default; }
  @media (max-height: 400px) {
    .bottom-panel {
      display: none; } }



