#map {
  position: absolute;
  width: 100%;
  height: 100%; }
  #map .ol-zoom {
    position: absolute;
    top: 50%;
    left: 0;
    -webkit-transform: translate(0, -50%);
        -ms-transform: translate(0, -50%);
            transform: translate(0, -50%);
    background-color: transparent; }
    #map .ol-zoom button {
      display: block;
      background-color: #fff;
      border: 2px solid #002f60;
      color: #002f60;
      margin: 5px;
      border-radius: 50%;
      width: 2rem;
      height: 2rem;
      line-height: 1.5rem;
      font-size: 1.5rem;
      cursor: pointer;
      -webkit-box-shadow: 0px 4px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 12px 1px rgba(0, 0, 0, 0.14), 0px 3px 15px 3px rgba(0, 0, 0, 0.12);
              box-shadow: 0px 4px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 12px 1px rgba(0, 0, 0, 0.14), 0px 3px 15px 3px rgba(0, 0, 0, 0.12); }
    @media (max-height: 640px), (max-width: 640px) {
      #map .ol-zoom {
        display: none; } }
  #map .ol-attribution {
    position: absolute;
    bottom: 3.25em;
    right: 0.5em;
    background-color: transparent; }
    @media (max-height: 400px) {
      #map .ol-attribution {
        bottom: 0.25em; } }
    #map .ol-attribution ul {
      font-size: 0.875em; }
    #map .ol-attribution a[href="https://openlayers.org/"] {
      display: none; }



